var that = this;
export default {
	data() {
		return {
			order_refund: {},
			express_index: '',
			express_list: [],
			modalName: '',
			info: '',
			express: ''
		};
	},
	methods: {
		hidden() {
			that.modalName = '';
		},
		prinexpress(e) {
			that.express_index = e.detail.value;
		},
		showModel(e) {
			that.modalName = 'kuaidi';
		},
		prinCat(e) {
			that.express_index = e.detail.value;
		},
		getinfo() {
			this.$request
				.request({
					url: that.api.anchor_order.detail,
					data: {
						order_id: that.id
					}
				})
				.then(res => {
					console.log(res);
					that.info = res;
					if (res.is_send) {
						that.getwl();
					}
				});
		},
		copyinfo: function(e) {
			console.log(e.currentTarget.dataset.info);
			uni.setClipboardData({
				data: e.currentTarget.dataset.info,
				success: function(t) {
					uni.showToast({
						title: '复制成功！',
						icon: 'success',
						mask: 'true'
					});
				},
				fail(res) {
					console.log(res);
				}
			});
		},
		fahuo(e) {
			let value = e.detail.value;
			let req = true,
				title = '';
			value.express ? '' : ((req = false), (title = '请选择快递公司'));
			value.express_no ? '' : ((req = false), (title = '请填写快递单号'));
			if (!req) {
				that.$showModal(title);
				return;
			}
			this.$request
				.request({
					url: that.api.anchor_order.send,
					data: {
						order_id: that.id,
						express: value.express,
						express_no: value.express_no
					},
					method: 'post'
				})
				.then(res => {
					console.log(res);
					that.hidden();
					that.$alertok('提交成功！');
					that.getinfo();
				});
		},
		getwl(orderinfo) {
			this.$request
				.request({
					url: that.api.anchor_order.express,
					data: {
						order_id: that.id
					},
					rejectType: true
				})
				.then(res => {
					that.express = res.list ? res.list : [];
				});
		}
	},
	onLoad(option) {
		that = this;
		that.id = option.id ? option.id : '1';
		let config = uni.getStorageSync('config');
		that.express_list = config.express;
		that.getinfo();
		console.log(config);
	}
};
